import { CreatePersonDto, RepresentativeDto } from "../types/dtos/person.dto";

export type PersonForm = {
  fullName: string;
  phone: string;
  email: string;
  address: string;
};

export type RepresentativeForm = PersonForm & {
  personId: number;
};

export const getDefaultPersonForm = (): PersonForm => ({
  fullName: '',
  phone: '',
  email: '',
  address: ''
});

export const getDefaultRepresentativeForm = (): RepresentativeForm => ({
  ...getDefaultPersonForm(),
  personId: 0
});

export const convertPersonFormToDto = (form: PersonForm): CreatePersonDto => ({
  fullName: form.fullName.trim(),
  phone: form.phone.trim() || null,
  email: form.email.trim() || null,
  address: form.address.trim() || null
});

export const convertRepresentativeFormToDto = (form: RepresentativeForm): RepresentativeDto => ({
  ...convertPersonFormToDto(form),
  personId: form.personId || null
});